.ground-start-rating {
  padding: 20px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  background: #ffff;
  border-radius: 8px;
  margin: 20px 0;
}
.ground-start-rating h3 {
  color: #4a5568;
  font-size: 1.2em;
  font-weight: 550;
  margin: 0;
  text-align: left;
  padding: 15px;
}
.ground-start-rating .main-start-rating {
  display: grid;
  grid-template-columns: 30% 70%;
  grid-gap: 10px;
  padding: 20px;
}
/* .ground-start-rating .main-start-rating .control-start-rating {
	border-left: 2px solid #4a5568;
} */
.main-start-rating .start-rating {
  align-self: center;
  text-align: center;
  justify-self: center;
}
.main-start-rating .start-rating h3 {
  font-size: 1.5em;
  color: #1890ff;
}
.main-start-rating .start-rating p {
  color: #cb1c22;
  font-size: 2em;
  margin: 0;
  font-weight: 550;
}
.ground-start-rating .main-start-rating .items-start-rating {
  display: grid;
  grid-template-columns: 15% 45% 37%;
  grid-column-gap: 10px;
}
.main-start-rating .items-start-rating .control-start {
  display: flex;
  justify-self: center;
}
.main-start-rating .items-start-rating .control-start p {
  margin-right: 5px;
  color: #4a5568;
  font-weight: 550;
}
.main-start-rating .items-start-rating p {
  margin: 0;
  color: #288ad6;
  font-size: 1em;
  justify-self: center;
}
.main-start-rating .items-start-rating p span {
  font-weight: 550;
}
@media screen and (max-width: 1000px) {
  .ground-start-rating {
    margin: 10px 10px;
  }
  .ground-start-rating {
    padding: 0;
  }
  .ground-start-rating .main-start-rating {
    padding: 10px;
  }
}
@media screen and (max-width: 480px) {
  .main-start-rating .start-rating h3 {
    font-size: 1.2em;
  }
}
@media screen and (max-width: 1100px) {
  .ground-start-rating .main-start-rating {
    grid-template-columns: 100%;
  }
  .ground-start-rating .main-start-rating .control-start-rating {
    border-right: none;
  }
}
